import Vue from 'vue'
import Router from 'vue-router'
const RongYu =()=>import('@/views/home/RongYu')
// const DaoHanItem =()=>import('@/components/DaoHanItem')
const JiShu =()=>import('@/views/home/JiShu')
const GongZ =()=>import('@/views/home/GongZ')
const XiangMu =()=>import('@/views/home/XiangMu')
const ShengH =()=>import('@/views/home/ShengH')
const QiTa =()=>import('@/views/home/QiTa')
const ShouYe =()=>import('@/components/ShouYe')
Vue.use(Router)

export default new Router({
  routes: [
    
    {
      path:'',
      redirect:'/shouye'
    },
    {
      path:'/shouye',
      component:ShouYe
    },
    {
      path:'/rongyu',
      component:RongYu
    },
    {
      path:'/jishu',
      component:JiShu
    },
    {
      path:'/gongz',
      component:GongZ
    },
    {
      path:'/shengh',
      component:ShengH
    },
    {
      path:'/xiangmu',
      component:XiangMu
    },
    {
      path:'/qita',
      component:QiTa
    },
    
  ],
  // mode:'history'
  mode:'hash'
})



