<template>
    <div @click="itemClick" :class="{'active':isActive}">
        <slot name="item"></slot>
    </div>
</template>

<script>

    export default {
        name: 'DaoHanItem',
        props:{
            path:String
        },
        methods:{
            itemClick(){
                this.$router.push(this.path)
            }
        },
        computed:{
            isActive(){
                return this.$route.path.indexOf(this.path) !== -1
            },
            // activeType(){
            //     return this.isActive ? {class:active} : {}
            //   }
        },
    }
</script>

<style scoped>

</style>


